<template>
  <div ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";

export default {
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 1,
    },
    /**
     * click
     */
    click: {
      type: Boolean,
      default: true,
    },
    /**
     * 
     */
    scrollX: {
      type: Boolean,
      default: false,
    },
    scrollBar: {
      type: Boolean,
      default: true,
    },
    /**
     * 
     */
    listenScroll: {
      type: Boolean,
      default: false,
    },
    /**
     * 
     */
    data: {
      type: Array,
      default: null,
    },
    /**
     * 
     */
    pullup: {
      type: Boolean,
      default: false,
    },
    /**
     * 
     */
    pulldown: {
      type: Boolean,
      default: false,
    },
    /**
     * 
     */
    beforeScroll: {
      type: Boolean,
      default: false,
    },
    /**
     * scroll
     */
    refreshDelay: {
      type: Number,
      default: 20,
    },
  },
  mounted() {
    // DOMbetter-scroll
    setTimeout(() => {
      this.initScroll();
    }, 20);
  },
  methods: {
    initScroll() {
      if (!this.$refs.wrapper) {
        return;
      }
      // better-scroll
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX,
        pullDownRefresh: this.pulldown,
        pullUpLoad: this.pullup,
        observeDOM: true, //  observe-dom 
        // observeImage: true //  observe-image 
        observeImage: {
          debounceTime: 200, // ms
        },
      });

      // 
      if (this.listenScroll) {
        this.scroll.on("scroll", (pos) => {
          this.$emit("scroll", pos);
        });
      }

      // 
      if (this.pullup) {
        this.scroll.on("pullingUp", () => {
          // await fetchData()
          // this.scroll.finishPullUp()
          this.$emit("pullUp");
        });
      }

      // 
      if (this.pulldown) {
        this.scroll.on("pullingDown", () => {
          // await fetchData()
          // this.scroll.finishPullDown()
          this.$emit("pullDown");
        });
      }

      // 
      if (this.beforeScroll) {
        this.scroll.on("beforeScrollStart", () => {
          this.$emit("beforeScroll");
        });
      }
    },
    getScrollY() {
      return this.scroll && this.scroll.y ? this.scroll.y : 0;
    },
    disable() {
      // better-scrolldisable
      this.scroll && this.scroll.disable();
    },
    enable() {
      // better-scrollenable
      this.scroll && this.scroll.enable();
    },
    refresh() {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh();
    },
    scrollTo() {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
    scrollToElement() {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
    },
    finishPullUp() {
      this.scroll && this.scroll.finishPullUp();
    },
    openPullUp() {
      this.scroll && this.scroll.openPullUp();
    },
    closePullUp() {
      this.scroll && this.scroll.closePullUp();
    },
    finishPullDown() {
      this.scroll && this.scroll.finishPullDown();
    },
    openPullDown() {
      this.scroll && this.scroll.openPullDown();
    },
    autoPullDownRefresh() {
      this.scroll && this.scroll.autoPullDownRefresh();
    },
  },
  watch: {
    // refreshDelayrefresh
    data() {
      setTimeout(() => {
        this.refresh();
      }, this.refreshDelay);
    },
  },
};
</script>

